// home svg file
.coder-svg {
    width: 12.5cm;
}

// home container primary 
.Home {
    padding-bottom: 2cm;
}

.wave-hand {
    rotate: 90deg;
}

// home container
.home-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1cm;
    padding: 10vh 0vh;
    // width: 80vw;
}

.bottom-link {
    color: gray;
    font-size: 18px;
    text-align: center;
}

// tagline home
.tagline {


    // color: rgba(255, 255, 255, 0.8);
    color: white;
    // transition: color 0.3s ease;

    // color: white;
    font-size: 70px;
    // font-weight: 600;
    letter-spacing: .05cm;
    font-family: bree serif;
    width: fit-content;

}

.tagline:hover {

    // color: linear-gradient(90deg, hsla(238, 100%, 71%, 1) 0%, hsla(295, 100%, 84%, 1) 100%);
    background: -webkit-linear-gradient(90deg, hsla(56, 83%, 59%, 1) 0%, hsla(307, 100%, 66%, 1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}

// subscribe button
.subscribe {
    font-size: 18px;
    padding: .2cm;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    // color: rgba(150, 175, 250, 0.921);
    background-color: rgba(162, 184, 251, 0.921);
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;


}

// scroll to top button
.scroll-to-top {
    background-color: gray;
}

.subscribe:hover {
    background-color: rgba(142, 170, 255, 0.921);
}

// sub tagline home
.sub-tagline {
    font-size: 35px;
    color: rgba(143, 171, 253, 0.921);
    // color: white;
    font-family: inika;
    // font-weight: 600;
    letter-spacing: .02cm;
}

// know more button
.know-more {
    background-color: #77dd77;
    padding: 5px 10px;
    font-size: 18px;
    // font-weight: 600;
    border: none;
    border-radius: 3px;
    box-shadow: inset 0 0 0.5px 1px hsla(0, 0%,
            100%, 0.075),
        /* shadow ring 👇 */
        0 0 0 1px hsla(0, 0%, 0%, 0.05),
        /* multiple soft shadows 👇 */
        0 0.3px 0.4px hsla(0, 0%, 0%, 0.02),
        0 0.9px 1.5px hsla(0, 0%, 0%, 0.045),
        0 3.5px 6px hsla(0, 0%, 0%, 0.09);

    cursor: pointer;

}

.know-more:hover {
    color: black;
    background-color: #6bbd6b;
}

// general headings
.headingg {
    color: rgba(211, 211, 211, 0.848);
    // color: rgb(21, 138, 97);
    font-family: bree serif;
    letter-spacing: .025cm;
    font-size: 35px;
    text-decoration: underline;
    // text-decoration-thickness: 2px;
}

// latest blog container home
.latest-blogs-container {
    padding-bottom: 10vh;
    padding-top: 2vh;
}

// latest blog details homepage
.blog-details-home {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2vh 2vw;
    border-bottom: .5px solid rgb(54, 53, 53);


}

// waving hand emoji
.wave-hand {
    transform: rotate(90deg);
}

.blog-name-home {
    // color: rgb(221, 219, 219);
    color: rgba(255, 255, 255, 0.8);

    // color: rgba(197, 193, 193, 0.951);
    font-size: 18px;
    // font-weight: 600;
    letter-spacing: .02cm;
    font-family: roboto;
    cursor: pointer;
}

.news-tag {
    color: rgb(184, 181, 181);
    // color: rgba(197, 193, 193, 0.951);
    font-size: 16px;
    // font-weight: 600;
    letter-spacing: 0px;
    font-family: roboto;

}

.blog-date-home {
    // color: rgb(145, 143, 143);
    color: #7b7b7b;
    // font-size: 18px;
    // font-weight: 600;
    letter-spacing: .03cm;
    font-family: roboto;
    font-size: 13px;
}

.blog-date-home-light {
    color: #3f3f3f;
}

.blog-name-home:hover {
    color: rgb(135, 135, 252);
}

// latest blog name container homepage
.blog-name-container-home {
    display: flex;
    align-items: baseline;
    column-gap: .1cm;
}


// redirect icon
.redirect {
    width: .22cm;
}

// show more line
.show-more {
    padding-left: 2vw;
    cursor: pointer;
    color: rgb(148, 148, 255);
}

.show-more:hover {

    color: rgb(105, 105, 251);
    // text-decoration: underline; 

}


// light mode code
.tagline-light {
    color: #000;
    font-weight: 600;



}

.tagline-light:hover {
    background: -webkit-linear-gradient(90deg, hsla(348, 82%, 49%, 1) 0%, hsla(356, 81%, 60%, 1) 23%, hsla(21, 83%, 65%, 1) 58%, hsla(31, 79%, 60%, 1) 86%, rgb(239, 144, 2) 100%);


    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

::-webkit-scrollbar-track {
    background: rgb(253, 251, 239);
}

.sub-tagline-light {
    color: blueviolet;
    // color: black;
    font-weight: 600;
}

.headingg-light {
    color: rgb(69, 68, 68);
}

.blog-details-home-light {
    border-bottom: .5px solid rgb(195, 193, 193);
}

.blog-name-home-light {
    color: black;
    font-weight: 600;
}

.blog-name-home-light:hover {
    color: black;
    text-decoration: underline;
}

.show-more-light {
    color: rgb(29, 29, 249);
    font-weight: 600;
}

.show-more-light:hover {
    color: rgb(29, 29, 249);
    text-decoration: underline;
}

.news-tag-light {
    color: black;

}

