// Primary container
.Projects {
    // padding-top: 10vh;
    padding: 2cm 0vw;
}

// projects container
.projects-container {
    padding: 1cm 0vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: .5cm;
    row-gap: .5cm;
}

// project box
.project {
    border: .1px solid rgb(63, 63, 63);

    width: 9cm;
    height: auto;
    padding: 0.5cm;
    // background-color: #1e1d1d;
    background-color: #242425;
 
    

   
    


    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;




}
// .project:hover{
    
//     box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(120, 119, 119) 0px 0px 0px 1px;


// }
.project-desc{
    padding-top: .3cm;
    font-size: 14px;
    color: rgb(220, 220, 220);
}

// project logo and name container 
.project-logo-container {
    display: flex;
    align-items: center;
    column-gap: .3cm;
}

.project-year {
    // padding-left: 1.6cm;
    padding-bottom: .1cm;
    color: rgb(196, 196, 0);
    font-size: 16px;
    text-align: right;
}

.project-logo {
    width: 1cm;
}

.project-name {
    font-size: 19px;
    // letter-spacing: .005cm;
    // color: rgba(197, 193, 193, 0.951);
    color: rgba(255, 255, 255, 0.92);

    font-family: fira sans;
    // font-weight: 600;

}
.project-name:hover{
    text-decoration: underline;
}
.project-tagline {
    font-size: 14px;
    // letter-spacing: .02cm;
    color: rgba(169, 169, 169, 0.989);
    font-family: roboto;
}

// container of button redirect to source code of project
.code {
    display: flex;
    align-items: baseline;
    column-gap: .08cm;
    border: .5px solid rgb(83, 83, 83);
    width: fit-content;
    padding: 1px 5px;
    border-radius: 3px;
    cursor: pointer;

    // background-color: #16161d;
    // background-color: #2c2b2b;

    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	// box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;



}
.code:hover{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(120, 119, 119) 0px 0px 0px 1px;

}

// primary container of buttons
.project-buttons {
    // margin-left: 2cm;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: .3cm;
    flex-wrap: wrap;
    row-gap: .4cm;

}
.header-project{
    display: flex;
    column-gap: .1cm;
    justify-content: flex-end;
    align-items: center;
}
.star{
    width: .32cm;
    height: .32cm;
}
// button redirect to source code of project
.code-link {
    // color:rgb(160, 160, 160);
    color: rgba(255, 255, 255, 0.922);


    cursor: pointer;
    font-size: 14px;
    padding: .05cm .05cm;
    // font-weight: 600;
    

.code-link:hover {
    text-decoration: underline;

}
}


// buttons redirect icon
.redirect{
    width: .23cm;
}
 

// light mode code
.project-light{
    // background-color: rgb(240, 239, 239);
    background-color: rgb(245, 245, 245); 

    border: none;
    color: black;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;
    // box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

    // box-shadow: none;
}
// .project-light:hover{
     
//     box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1.2px, rgb(97, 96, 96) 0px 0px 0px 1.2px;

// }
.project-name-light{
    color: black;
    letter-spacing: 0cm;
}
.project-tagline-light{
    color: rgb(68, 68, 68);

}
.project-desc-light{
    color: rgb(35, 35, 35);

}
.code-link-light {
    color:black;
    cursor: pointer;
    font-size: 13px;
    // font-weight: 500;
    padding: .05cm .05cm;

    font-weight: 600;
    

.code-link-light:hover {
    text-decoration: underline;

}
}

.code-light {
    
    border: none;
    width: fit-content;
   border: thin rgb(176, 176, 176) solid;
    border-radius: 3px;
    cursor: pointer;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;


    // background-color: #16161d;
    // background-color: #2c2b2b;

    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	// box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;



}
.code-light:hover{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1.2px, rgb(97, 96, 96) 0px 0px 0px 1.2px;

}





@media screen and (max-width:900px) {
    .project {
        width: 8cm;
    }
   
}
@media screen and (max-width:800px) {
    .project {
        width: 7cm;
    }

    
}

@media screen and (max-width:700px) {
    .project {
        width: 70vw;
    }
    .code-link{
        font-weight: 500;
    }
    
}

@media screen and (max-width:600px){
    
    .project {
        width: 85vw;
    }
    .code-link{
       
        padding: .12cm .05cm;
    }

}

@media screen and (max-width:400px) {
    .project {
        width: 90vw;
    }

    .project-logo {
        width: .9cm;
    }
    .code-link{
       
        padding: .08cm .03cm;
    }

    
}
@media screen and (max-width:300px) {
   
    .project{
        padding: .3cm;
    }
    
}