.Newsletter{
    padding: .7cm 0vw;
    .contact-container{
        box-shadow: none;
        background-color: transparent;
        padding: 0cm;
        border: none;
    }
}
.news-text{
    color: rgba(197, 193, 193, 0.951);

    font-family:roboto;
    font-weight: 500;
    text-align: center;
}
.news-sub{
    color: white;
    padding-top: .2cm;
    text-align: center;
}
.news-sub-light{
    color: black;
}
.newsletter{
    width: fit-content;
    padding: .7cm;
    display: flex;
    align-items: center;
    justify-content: center;
}
.news-text-light{
    color: black;
}
.news-container {
    border: .1px solid rgb(50, 49, 49);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    height: auto;
    padding: .8cm;
    background-color: #2a2929d5;





    border-radius: 3px;
    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

}

@media screen and (max-width:600px){
    .Newsletter{
        padding: .5cm 0vw;
    }
}