// primary container
.Skills {
    padding: 2cm 0vw;
}

// skills container
.skills-container {
    padding-top: 1cm;
    display: flex;
    justify-content: center;
    column-gap: .6cm;
    row-gap: .6cm;
    flex-wrap: wrap;
}

// skill card
.skill {
    border: .5px solid #636367;
    width: 3.3cm;
    padding: .3cm;
    height: fit-content;
    background-color: #242425;






    border-radius: 3px;

    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: .1px solid rgb(50, 49, 49);
}

.skill:hover {
    .skill-img {
        transform: scale(1.15);
    }

}



