// primary container
.About {
    padding: 10vh 0vw;
}

// About me division
.about-me {
    padding: .5cm;
    // color: rgb(186, 183, 183);
    // color: whitesmoke;
    color: rgba(255, 255, 255, .85);

    border: .5px solid rgb(41, 40, 40);
    // font-family: roboto;
    font-size: 18px;
    background-color: #242425;





    border-radius: 3px;
    display: flex;
    flex-direction: column;
    row-gap: 1cm;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

    


}

h2{
    // font-family: bree serif;
    margin: 0px 0px;
    // font-weight: 500;
    font-weight: 600;
}
// primary about container
.about-me-container {
    padding: 5vh 0vw;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    // align-items: flex-start;
    // column-gap: 1.5cm;
}
.headingg-about{
    font-size: 35px;
    font-weight: 600;
}
// click to go to skill page
.skill-click {
    
    color: rgba(137, 167, 255);
    // font-weight: 600;

}

.skill-click:hover {
    text-decoration: underline;
}

// my photo
.my-pic {
    width: 7cm;
    pointer-events: none;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;

    height: 7cm;
    border-radius: 500px;
    // border: .5px solid rgb(41, 41, 41);
    &:hover::after{
        content: "hii";
        color: white;
    }
}

// light mode code
.about-me-light{
    background-color: rgb(245, 245, 245); 
    border: none;
    color: black;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;
    // box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;


}
.skill-click-light{
    color: blue;
}
.skill-click-light:hover{
    color: blue;
    text-decoration: underline;
}


@media screen and (max-width:1200px) {
    .my-pic {
        width: 6cm;
        height: 6cm;
    }
    .about-me-container{
        column-gap: 1cm;
    }
    .about-me{
        font-size: 17px;
    }
    
}
@media screen and (max-width:1000px) {
    .my-pic {
        width: 5cm;
        height: 5cm;
    }
   
}
@media screen and (max-width:900px){
    .about-me-container{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
    }
    .about-me{
        font-size: 16px;
    }
    .headingg-about{
        font-size: 33px;
        
    }
    
}
@media screen and (max-width:600px){
    
   
    .about-me{
        font-size: 15.5px;
    }
    .headingg-about{
        font-size: 30px;
        
    }
}
@media screen and (max-width:500px){
    .about-me{
        width: 90vw;
    }
    .about-me{
        font-size: 15px;
    }
    .headingg-about{
        font-size: 28px;
        
    }
}
@media screen and (max-width:400px){
    
   
    .my-pic{
        width: 4cm;
        height: 4cm;
    }
 
}
@media screen and (max-width:300px){
    .about-me{
        font-size: 14px;
    }
    .headingg-about{
        font-size: 25px;
        
    }
    
}