.Contact {
    padding: 2cm 0vw;
}

// contact box primary container
.contact-box-container {
    padding-top: 5vh;
    display: flex;
    justify-content: center;
}

// social icons container
.social-icons {
    display: flex;
    column-gap: .35cm;
    flex-wrap: wrap;
    row-gap: .3cm;
}

// social icons
.social-icon {
    width: .8cm;
    height: .8cm;
}

.sub-search {
    color: rgb(184, 183, 183);
}

// contact box container
.contact-container {
    border: .1px solid rgb(50, 49, 49);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    height: auto;
    padding: .8cm;
    background-color: #242425;





    border-radius: 3px;
    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

}

input[type="text"],
input[type="password"],
input[type="email"],
textarea {
    border: none;
    outline: none;
    border-bottom: 1px solid #828181;
    /* You can adjust the color and thickness of the outline */
}





// contact form
.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: .5cm;

    input[type=text]:focus,
    input[type=email]:focus,
    textarea:focus {
        border-bottom: 1.5px solid rgba(143, 171, 253, 0.921);
    }
}

.contact-form-light {

    input[type=text]:focus,
    input[type=email]:focus,
    textarea:focus {
        border-bottom: 2px solid blueviolet;
    }
}

// inputs
.contact-inputs {
    width: 7cm;
    background-color: transparent;
    font-size: 14px;
    padding: .2cm;
    // border-radius: 3px;
    border: none;
    color: rgba(255, 255, 255, 0.876);
    border-bottom: thin grey solid;



    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;


    font-family: roboto;

}

// contact form submit button
.submit-contact {
    // background-color: rgb(183, 117, 246);

    width: 100%;
    font-size: 15px;
    border: none;
    border-radius: 3px;
    padding: .25cm .5cm;
    background-color: rgba(143, 171, 253, 0.921);
    color: black;


    cursor: pointer;
    // color: rgba(255, 255, 255, 0.815);
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;

}

.submit-contact:hover {
    
    background-color: rgba(111, 146, 250, 0.921);





}

.email-at {
    color: rgb(197, 196, 196);
}

.contact-inputs-light {
    color: black;
}

.contact-container-light {
    background-color: rgb(245, 245, 245);
    border: none;
    color: black;
    // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 3px, rgba(0, 0, 0, 0.23) 0px 1px 3px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;


}

.submit-contact-light {
    background-color: blueviolet;
    color: white;


}

.submit-contact-light:hover {
    background-color: rgb(131, 23, 232);
   


}

.email-at-light {
    color: black;
}


@media screen and (max-width:600px) {
    .contact-inputs {
        width: 7cm;
    }

    .social-icon {
        width: .8cm;
        height: .8cm;
    }

    .sub-search {
        font-size: 14px;
    }

    .email-at {
        font-size: 14px;
    }

    .contact-container {
        padding: .5cm;
    }
}

@media screen and (max-width:500px) {
    .contact-inputs {
        width: 55vw;
        font-size: 14px;
    }

    .social-icon {
        width: .7cm;
        height: .7cm;
    }

    .submit-contact {
        font-size: 15px;
    }

}

@media screen and (max-width:350px) {
    .contact-inputs {
        width: 60vw;
        font-size: 14px;
    }

    .social-icon {
        width: .65cm;
        height: .65cm;
    }

}