.Navbar{
    padding-top: .3cm;
}

// logo
.logo {
    width: .9cm;
}

// navigation bar's left division
.left-nav {
    display: flex;
    column-gap: .2cm;
    align-items: center;
}

// my name
.name{
    color: rgb(208, 206, 206);
    font-family: raleway;
    font-weight: 600;
    font-size: 20px;
    letter-spacing: .015cm;
}
.name:hover{
    color: rgb(243, 241, 241);
}
// navigation bar's menu item
.menuItem{
    color: rgb(182, 177, 177);
    cursor: pointer;
    font-size: 14px;
    transition: .1s;
}
.menuItem:hover{
    color: rgb(243, 241, 241);
    // font-size: 23px;
}
// navigation menu
.menu{
    display: flex;
    align-items: center;
    column-gap: .5cm;
}

// navigation menu container
.nav-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 0px;
    // border-bottom: 1px solid rgb(63, 62, 62);
}

// link of nav menu to component
.link-nav{
    text-decoration: none;
    margin: 0px 0px;
}



.overlay {
	height: 100%;
	width: 0%;
	position: fixed;
	z-index: 1;
	top: 0;
	right: 0;
	background-color: #3b3939;
	overflow-y: hidden;
	transition: 0.5s;
}
.mt{
    color: rgb(236, 234, 234);
}


.overlay-content {
	position: relative;
	top: 15%;
	width: 100%;
	text-align: center;
	margin-top: 30px;
}

.overlay p {
	padding: 8px;
	text-decoration: none;
	font-size: 16px;
	color: rgb(239, 5, 5);
	display: block;
	transition: 0.3s;
	font-family: Fira Sans;
}

.overlay p:hover,
.overlay p:focus {
	color: #f1f1f1;
}

.overlay .closebtn { 
	position: absolute;
	top: 10px;
	right: 45px;
	font-size: 60px;
}

.mode{
    width: .55cm ;
    cursor: pointer;
   
}


.menu-icon {
	height: .8cm;
	display: none;
	// box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
	// border-radius: 10px;
}

// light mode code
.name-light{
    color: rgb(68, 68, 68);
    font-weight: 600;
}
.name-light:hover{
    color: rgb(0, 0, 0);
}

.menuItem-light{
    color: rgb(32, 32, 32);
    // font-weight: 600;


    &:hover{
    color: rgb(0, 0, 0);

    }
}
// .nav-container-light{
//     border-bottom: .5px solid rgb(195, 193, 193);

// }




@media screen and (max-width:900px){
    .name{
        font-size: 20px;
    }
    .logo{
        width: .9cm;
    }
    .menu-icon{
        width: .6cm;
        height: .6cm;
    }
    .mode{
        width: .65cm;
    }
}

@media screen and (max-width: 800px) {

	.menuItem{
        display: none;
    }
	.menu-icon {
		display: block;
		cursor: pointer;
	}
    
}
@media screen and (max-width:600px){
    .menu-icon{
        width: .5cm;
        height: .5cm;
    }
    .mode{
        width: .55cm;
    }
    .name{
        font-size: 18px;
    }
    .logo{
        width: .9cm;
    }
}

@media screen and (max-width:400px){
    .name{
        display: none;
    }
    .logo{
        width: .8cm;
    }
    .menu-icon{
        width: .42cm;
        height: .42cm;
    }
    .mode{
        width: .45cm;
    }
    .overlay p {
        padding: 8px;
        text-decoration: none;
        font-size: 14px;
        color: rgb(239, 5, 5);
        display: block;
        transition: 0.3s;
        font-family: Fira Sans;
    }
}